import * as React from 'react';
import { IntlProvider } from 'react-intl';
import { FormattedMessage as F } from 'react-intl';
import './App.scss';
import { Table } from './components/table';
import {
  // eos_instance,
  network,
} from './utils/eos';
// import { banker_account } from './utils/config';
import { getLocale } from './locale';
import { Header } from './components/header';
import Eos from 'eosjs';
import * as UUID from 'uuid';
import { BetGame } from './components/bet_game';
import { banker_account } from './utils/config';
import { Footer } from './components/footer';

interface AppProps {}

export type langType = 'zh-CN' | 'en-US';

interface AppState {
  table_data:any[];
  lang:langType;
  scatter:any;
  show_toast:boolean;
  toast_tips:any;
  player_name?:string;
}

class App extends React.Component<AppProps, AppState> {
  
  protected time_id:any;
  
  public constructor(props:AppProps) {
    super(props);
    this.state = {
      table_data:[],
      lang:'zh-CN',
      scatter:undefined,
      show_toast:false,
      toast_tips:'',
    };
    document.addEventListener('scatterLoaded', scatterExtension => {
      console.warn('scatter:', (window as any).scatter);
      const scatter = (window as any).scatter;
      this.setState({scatter}, () => {
        if (scatter.identity && scatter.identity.accounts) {
          const account_name = scatter.identity.accounts.find((account:any) => account.blockchain === 'eos').name;
          this.setState({
            player_name:account_name,
          });
        } else {
          this.login();
        }
      });
    });
  }
  
  public componentDidMount() {
    app_instance = this;
  }
  
  public render() {
    const appLocale = getLocale(this.state.lang);
    return (
      <IntlProvider
        locale={appLocale.locale}
        messages={appLocale.messages}
        formats={appLocale.formats}
      >
        <div className="App">
          <Header
            onLogout={this.logout}
            selected_land={this.state.lang}
            onLogin={this.login}
            on_change_lang={this.changeLang}
            player_name={this.state.player_name}
          />
          <BetGame
            player_name={this.state.player_name}
            scatter={this.state.scatter}
            lang={this.state.lang}/>
          <Table player_name={this.state.player_name}/>
          {
            this.state.show_toast &&
            <div className='toast'>
              {this.state.toast_tips}
            </div>
          }
          <Footer/>
        </div>
      </IntlProvider>
    );
  }
  
  public show_toast = (tips:any) => {
    this.setState({
      show_toast:true,
      toast_tips:tips,
    }, () => {
      if (this.time_id) {
        clearTimeout(this.time_id);
      }
      this.time_id = setTimeout(() => {
        this.setState({show_toast:false, toast_tips:''});
      }, 3000)
    })
  }
  
  private logout = () => {
    if (this.state.scatter) {
      this.state.scatter.forgetIdentity().then(() => {
        this.setState({
          player_name:undefined,
        });
      });
    }
  }
  
  public login = () => {
    if (this.state.scatter) {
      this.state.scatter.getIdentity({ accounts: [network] }).then(() => {
        const scatter = (window as any).scatter;
        this.setState({
          scatter,
        }, () => {
          if (scatter.identity && scatter.identity.accounts) {
            const account_name = scatter.identity.accounts.find((account:any) => account.blockchain === 'eos').name;
            this.setState({
              player_name:account_name,
            });
          }
        });
      }).catch(() => {
        this.show_toast(<F id='cancel_login'/>);
      });
    } else {
      this.show_toast(<F id='scatter'/>);
    }
  }
  
  private changeLang = (lang:langType) => {
    this.setState({lang});
  }
}

export default App;

export let app_instance:App;



