import * as React from 'react';
import { FormattedMessage as F } from 'react-intl';
import * as dayjs from 'dayjs';
import { eos_instance } from '../utils/eos';
import { banker_account } from '../utils/config';
import { delay } from '../utils/'
import { reverse} from 'lodash';
import './table.scss';
import dice_1 from 'src/assets/table_dices/dice_1.svg';
import dice_2 from 'src/assets/table_dices/dice_2.svg';
import dice_3 from 'src/assets/table_dices/dice_3.svg';
import dice_4 from 'src/assets/table_dices/dice_4.svg';
import dice_5 from 'src/assets/table_dices/dice_5.svg';
import dice_6 from 'src/assets/table_dices/dice_6.svg';
import uuid from 'uuid';

export const dices = [
  dice_1,
  dice_2,
  dice_3,
  dice_4,
  dice_5,
  dice_6
];

interface TableProps {
  player_name?:string;
}

interface TableState {
  data:TableItems[];
  show_all_bets:boolean;
}

export interface ApiRes {
  player:string;
  payin:string;
  payout:string;
  dice:{ // 开奖结果
    dice1:number;
    dice2:number;
    dice3:number;
  };
  payed:1|0;
  uid:string;
  detail:string; // 下注信息
  
}

interface TableItems {
  uid:string;
  time:dayjs.Dayjs;
  player:string;
  result:number[];
  betting:string;
  win:string;
}

export class Table extends React.Component<TableProps, TableState> {
  protected rows_count = 20;
  public constructor(props:TableProps) {
    super(props);
    this.state = {
      data:[],
      show_all_bets:true,
    };
    this.get_new_actions = this.get_new_actions.bind(this);
  }
  
  public componentDidMount() {
    this.get_new_actions_loop();
  }
  
  public async get_new_actions() {
    if (this.state.show_all_bets) {
    
    }
    const search_account = this.state.show_all_bets?banker_account:this.props.player_name;
    // 操作记录
    eos_instance.getActions(banker_account, -1 , -200).then(({actions}:any) => {
      const result:TableItems[] = [];
      reverse(actions).forEach((action:any) => {
        if (
          result.length < this.rows_count
          && action.action_trace
          && action.action_trace.act
          && action.action_trace.act.account === banker_account
          && action.action_trace.act.name === 'result'
          && action.action_trace.act.data
          && action.action_trace.act.data.res
          && action.action_trace.receipt
          && action.action_trace.receipt.receiver === banker_account) {
          const res:ApiRes = action.action_trace.act.data.res;
          const block_time_dayjs = dayjs(action.block_time);
          const data:TableItems = {
            time:dayjs(new Date(
              Date.UTC(
                block_time_dayjs.year(),
                block_time_dayjs.month(),
                block_time_dayjs.date(),
                block_time_dayjs.hour(),
                block_time_dayjs.minute(),
                block_time_dayjs.millisecond()
              )
            )),
            player:res.player,
            result:[
              res.dice.dice1,
              res.dice.dice2,
              res.dice.dice3,
            ],
            betting:res.payin,
            win:res.payout,
            uid:res.uid,
          };
          result.push(data);
        }
      })
      this.setState({
        data:result,
      })
    }).catch(() => {
      // alert('error');
    });
  }
  
  private get_new_actions_loop = async () => {
    await this.get_new_actions();
    await delay(3000);
    this.get_new_actions_loop()
  }
  
  public render(){
    let list;
    if (!this.state.show_all_bets) {
      list = this.state.data.filter((item) => {
        return item.player === this.props.player_name
      }).map((item) => {
        return this.render_row(item);
      });
    } else {
      list = this.state.data.map((item) => {
        return this.render_row(item);
      })
    }
    if (list.length === 0) {
      list.push((
        <tr><td colSpan={5}><F id='empty'/></td></tr>
      ));
    }
    return (
      <div className='table_wrapper'>
        <div className='table_title'>
          <div onClick={() => {
            this.setState({show_all_bets:true});
          }} className={this.state.show_all_bets ? 'selected' : ''}>
            <F id='all_bets'/>
          </div>
          <div onClick={() => {
            this.setState({show_all_bets:false});
          }} className={!this.state.show_all_bets ? 'selected' : ''}>
            <F
              id='my_bets'
            />
          </div>
        </div>
        <table className={'best-table'} cellSpacing={0}>
          <thead>
          <tr>
            <th align='center' className='time_row'>
              <F
                id='time'
              />
            </th>
            <th align='center' className='player_row'>
              <F
                id='player'
              />
            </th>
            <th align='center' className='lottery_row'>
              <F
                id='lottery'
              />
            </th>
            <th align='center' className='betting_row'>
              <F
                id='betting'
              />
            </th>
            <th className='win_row' align='center'>
              <F
                id='win'
              />
            </th>
          </tr>
          </thead>
          <tbody>
          {list}
          </tbody>
        </table>
      </div>
    );
  }
  
  private render_row = (item:TableItems) => {
    return (
      <tr key={uuid.v4()}>
        <td>{item.time.format('MM-DD HH:mm:ss')}</td>
        <td>{item.player}</td>
        <td className='dice_row'>{this.render_bets(item.result)}</td>
        <td>{item.betting}</td>
        <td>{/^(0.0000 EOS)/.test(item.win)?'':item.win}</td>
      </tr>
    );
  }
  
  private render_result_tips(result:number[]) {
    const total = result[0] + result[1] + result[2];
    let tips = total > 10 ? <F id='bigV' values={{total}}/> : <F id='smallV' values={{total}}/>;
    if (result[0] === result[1] && result[1] === result[2]) {
      tips = <F id='triple' values={{total}}/>
    }
    return tips;
  }
  
  private render_bets(result:number[]) {
    const lists = result.map((number, index) => {
      return (
        <img src={dices[number -1]}/>
      );
    });
    return (
      <span className='bets'>
        {lists}
      </span>
    );
  }
}


