import * as React from 'react';
import { FormattedMessage as F } from 'react-intl';
import { langType } from '../App';
import { ShareDialog } from './share_dialog';
import china_icon from 'src/assets/country/china.png';
import us_icon from 'src/assets/country/us.png';
import pop_icon from 'src/assets/pop.png';
import menu_icon from 'src/assets/menu.png';
import chinese from 'src/assets/country/chinese.png';
import chinese_selected from 'src/assets/country/chinese_selected.png';
import english from 'src/assets/country/english.png';
import english_selected from 'src/assets/country/english_selected.png';
import logo from 'src/assets/logo.png';
import popup_mask from 'src/assets/tablecloth/table_mask.png';
import close from 'src/assets/close.png';
import './header.scss';

interface HeaderProps {
  on_change_lang:(lang:langType) => void;
  player_name?:string;
  onLogin:() => void;
  onLogout:() => void;
  selected_land:langType;
}

interface HeaderState {
  player_name?:string;
  show_options:boolean
  show_menu_options:boolean;
  show_logout_btn:boolean;
  show_rule_tips:boolean;
  show_share_dialog:boolean;
}

interface LandTypeOption {
  lang:langType;
  icon:any;
  option_icon:any;
  selected_option_icon:any;
  text:string;
}

const langTypeOptions:LandTypeOption[] = [
  {
    icon:china_icon,
    lang:'zh-CN',
    option_icon:chinese,
    selected_option_icon:chinese_selected,
    text:'繁体中文',
  },
  {
    icon:us_icon,
    lang:'en-US',
    option_icon:english,
    selected_option_icon:english_selected,
    text:'English',
  }
];

export class Header extends React.Component<HeaderProps, HeaderState > {
  private lang_btn_ref:React.RefObject<any>;  
  private menu_btn_ref:React.RefObject<any>;
  private logout_btn_ref:React.RefObject<any>;

  public constructor(props:HeaderProps) {
    super(props);
    this.on_change_lang = this.on_change_lang.bind(this);
    this.render_select_lang_btn = this.render_select_lang_btn.bind(this);
    this.change_options_visible = this.change_options_visible.bind(this);
    this.state = {
      show_options:false,
      show_menu_options:false,
      show_logout_btn:false,
      show_rule_tips:false,
      show_share_dialog:false,
    };
    document.addEventListener('mouseup', (e) => {
      if (this.state.show_logout_btn) {
        if (e.target != this.logout_btn_ref.current) {
          setTimeout(()=>{
            this.setState({show_logout_btn:false});
          }, 10)
        }
      }

      if (this.state.show_menu_options) {
        if (e.target != this.menu_btn_ref.current) {
          setTimeout(()=>{
            this.setState({show_menu_options:false});
          }, 10);
        }
      }
      
      if (this.state.show_options) {
        if (e.target != this.lang_btn_ref.current) {
          setTimeout(() => {
            this.setState({show_options:false});
          },10);
        }
      }
    });
    this.lang_btn_ref = React.createRef();
    this.menu_btn_ref = React.createRef();
    this.logout_btn_ref = React.createRef();
  }
  
  public render(){
    return (<div className='header_wrapper'>
      { this.render_logo() }
      { this.render_login_btn() }
      { this.render_select_lang_btn() }
      { this.render_menu_btn() }
      { this.state.show_options && this.render_options() }
      { this.state.show_menu_options && this.render_menu_options() }
      { this.state.show_logout_btn && this.render_logout_btn() }
      { this.state.show_rule_tips && this.render_rule_tips() }
      { this.state.show_share_dialog && <ShareDialog player_name={this.props.player_name} on_closed={() => {this.setState({show_share_dialog:false})}}/> }
    </div>);
  }
  
  private render_rule_tips() {
    return (
      <div style={{backgroundImage:`url(${popup_mask})`}} className='popup'>
        <div className='window'>
          <div className='header'>
            <text>
              <F id='game_rules'/>
            </text>
            <img onClick={() => {
              this.setState({show_rule_tips:false});
            }} src={close} className='close'/>
          </div>
          <div className='content_wrapper'>
            <div className='content'>
              <div className='rule'><F id='rule1'/></div>
              <div className='rule'><F id='rule2'/></div>
              <div className='rule'><F id='rule3'/></div>
              <div className='rule_tips'><F id='rule_tips'/></div>
            </div>
          </div>
        </div>
      </div>
    );
  }
  
  private render_logo() {
    return (
      <img className='logo' src={logo}/>
    );
  }
  
  private render_menu_options = () => {
    return (
      <div className='header_menu_options'>
        <div
          onClick={() => {
            this.setState({show_menu_options:false, show_rule_tips:true});
          }}
        >
          <F id='rule'/>
        </div>
        <div
          onClick={() => {
            this.setState({show_menu_options:false, show_share_dialog:true});
          }}
        >
          <F id='money'/>
        </div>
      </div>
    );
  }
  
  private render_logout_btn = () => {
    return (
      <div onClick={() => {
        this.setState({show_logout_btn:false});
        this.props.onLogout();
      }} className='logout'>
        <F tagName='text' id='logout'/>
      </div>
    );
  }
  
  private render_login_btn() {
    if (this.props.player_name) {
      return (<span ref={this.logout_btn_ref} onClick={() => {
        this.setState({show_logout_btn:!this.state.show_logout_btn});
      }} className='header_login'>{this.props.player_name}</span>)
    }
    return (
      <span className='header_login' onClick={this.props.onLogin}>
        <F id='login'/>
      </span>
    );
  }
  
  private render_menu_btn = () => {
    return (
        <img ref={this.menu_btn_ref} onClick={() => {
          this.setState({show_menu_options:!this.state.show_menu_options});
        }} className='header_menu_btn' src={menu_icon}/>
    );
  }
  
  private render_select_lang_btn() {
    const { selected_land } = this.props;
    const icon = langTypeOptions.find((option) => {
      return option.lang === selected_land;
    });
    return (
      <div ref={this.lang_btn_ref} onClick={this.change_options_visible} className='header_select_lang_btn'>
        <img className='select_lang_btn_im1' src={(icon as LandTypeOption).icon} />
      </div>
    );
  }
  
  private change_options_visible() {
    const { show_options } = this.state;
    this.setState({show_options:!show_options});
  }
  
  private render_options() {
    const options = langTypeOptions.map((item) => {
      return this.render_option(item);
    });
    return (
      <div className='header_options_wrapper'>
        {options}
      </div>
    );
  }
  
  private render_option(option:LandTypeOption) {
    const { selected_land } = this.props;
    const is_selected = selected_land === option.lang;
    return (
      <span onClick={this.select_option.bind(this, option.lang)} className='header_option' key={option.lang}>
        <img src={option.icon}/>
        <text>{option.text}</text>
      </span>
    );
  }
  
  private select_option(lang:langType) {
    this.setState({show_options:false});
    this.props.on_change_lang(lang);
  }
  
  private on_change_lang(event:any) {
    this.props.on_change_lang(event.target.value);
  }
}



