import zh from './zh/';
import en from './en/'

export function getLocale(lang:string) : {
  messages:object,
  locale:string,
  formats:object,
}{
  const rules = {
    'zh-CN':zh,
    'en-US':en
  };
  if (lang in rules) {
    return rules[lang];
  }
  return rules['zh-CN'];
}



