import * as React from 'react';

interface TableDiceProps {
  number:number;
}

interface TableDiceState {
}

const dices = [
  <div className='dice_1'>
    <div/>
  </div>,
  <div className='dice_2'>
    <div/>
    <div className='two'/>
  </div>,
  <div className='dice_3'>
    <div/>
    <div className='dice_3_2'/>
    <div className='dice_3_3'/>
  </div>,
  <div className='dice_4'>
    <div>
      <span/>
      <span/>
    </div>
    <div>
      <span/>
      <span/>
    </div>
  </div>,
  <div className='dice_5'>
    <div>
      <span/>
      <span/>
    </div>
    <div className='middle'>
      <span/>
    </div>
    <div>
      <span/>
      <span/>
    </div>
  </div>,
  <div className='dice_6'>
    <div>
      <span/>
      <span/>
      <span/>
    </div>
    <div>
      <span/>
      <span/>
      <span/>
    </div>
  </div>
];

export class TableDice extends React.Component<TableDiceProps, TableDiceState > {
  
  public constructor(props:TableDiceProps) {
    super(props);
  }
  
  public render(){
    return dices[this.props.number - 1];
  }
}


