import * as React from 'react';
import { common_images } from './imgae_config';
import anime from 'animejs';

export interface CurrencyProps {
  currency_type:number;
  end_position:{
    top:number;
    left:number;
  },
}

interface CurrencyState {
}

const currency_types = {
  1:common_images.btn_currency_1,
  10:common_images.btn_currency_50,
  100:common_images.btn_currency_100,
};

const start_positions = {
  1:{
    top:610,
    left:25,
  },
  10:{
    top:610,
    left:80,
  },
  100:{
    top:610,
    left:130,
  },
};


export class Currency extends React.Component<CurrencyProps, CurrencyState > {
  protected img_ref:React.RefObject<any>;
  public constructor(props:CurrencyProps) {
    super(props);
    this.state = {
    };
    this.img_ref = React.createRef();
  }
  
  public componentDidMount() {
    anime({
      targets:this.img_ref.current,
      top:this.props.end_position.top,
      left:this.props.end_position.left,
      easing: 'linear',
      duration:100,
    });
  }
  
  public render(){
    const { currency_type } = this.props;
    return (
      <img
        className='bet_currency'
        ref={this.img_ref}
        style={{
          position:'absolute',
          pointerEvents:'none',
          ...start_positions[currency_type],
        }}
        src={currency_types[currency_type]}
      />
    );
  }
  
  public async move_to(position:{top:number|string;left:number|string;}) {
    await new Promise((resolve) => {
      anime({
        targets:this.img_ref.current,
        top:position.top,
        left:position.left,
        easing: 'linear',
        duration:200,
        complete:() => {
          resolve();
        }
      });
    });
  }
}


