import * as React from 'react';
import mask from 'src/assets/result/mask@3x.png';
import en_lose from 'src/assets/result/en_lose.png';
import cn_lose from 'src/assets/result/cn_lose.png';
import cn_win from 'src/assets/result/cn_win.png';
import en_win from 'src/assets/result/en_win.png';
import { langType } from '../../App';

const imgs:{[key:string]:{lose:any;win:any}}= {
  "zh-CN":{
    lose:cn_lose,
    win:cn_win,
  },
  "en-US":{
    lose:en_lose,
    win:en_win
  },
};

interface ResultProps {
  win:boolean;
  result:string;
  lang:langType;
}

interface ResultState {
}


export class Result extends React.Component<ResultProps, ResultState > {
  
  public constructor(props:ResultProps) {
    super(props);
    this.state = {
    };
  }
  
  public render(){
    const { win, result } = this.props;
    return (<div style={styles.wrapper}>
      <img style={{width:328, position:'absolute',top:100}} src={win ? imgs[this.props.lang].win : imgs[this.props.lang].lose} />
      <div className={win?'win_result_tips':'lose_result_tips'} style={{position:'absolute', color:'rgba(254,254,254,1)',top:win?240:300}}>
        {win ? '+' : '-'}&nbsp;
        {result}
      </div>
    </div>);
  }
  
}

const styles:{[key:string]:React.CSSProperties} = {
  wrapper:{
    flexDirection:'column',
    display:'flex',
    position:'fixed',
    backgroundImage:`url(${mask})`,
    backgroundSize:'cover',
    width:'100%',
    height:'100%',
    justifyContent:'center',
    alignItems:'center',
    zIndex:20,
  }
};


