import * as React from 'react';
import anime from 'animejs';
import dice1 from 'src/assets/dices/dice_1.png';
import dice2 from 'src/assets/dices/dice_2.png';
import dice3 from 'src/assets/dices/dice_3.png';
import dice4 from 'src/assets/dices/dice_4.png';
import dice5 from 'src/assets/dices/dice_5.png';
import dice6 from 'src/assets/dices/dice_6.png';

const resultDices = [dice1, dice2, dice3, dice4, dice5, dice6];

interface ResultDiceProps {
  number:number;
  style?:React.CSSProperties;
  className?:string;
}

interface ResultDiceState {}

export class ResultDice extends React.Component<ResultDiceProps, ResultDiceState > {
  protected img_ref:React.RefObject<any>;
  public constructor(props:ResultDiceProps) {
    super(props);
    this.img_ref = React.createRef();
  }
  
  public componentDidMount() {
    anime({
      targets:this.img_ref.current,
      rotate:[-80,0],
      duration:350,
    });
  }
  
  public render(){
    const style = {
      position:'absolute' as 'absolute',
      width:46,
      height:47,
      ...this.props.style,
    };
    return (
      <img className={this.props.className} ref={this.img_ref} src={resultDices[this.props.number - 1]} style={style}/>
    );
  }
}



