import * as appLocaleData from 'react-intl/locale-data/en';
import messages from './zh-Hans.messages';

export default {
  messages:{
    ...messages,
  },
  
  // locale
  locale: 'en-US',
  
  // react-intl locale-data
  data: appLocaleData,
  
  // 自定义 formates
  formats: {
    date: {
      normal: {
        hour12: false,
        year: 'numeric',
        month: '2-digit',
        day: '2-digit',
        hour: '2-digit',
        minute: '2-digit',
      },
    },
    // 货币
    money: {
      currency: 'USD',
    },
  },
};



