import * as React from 'react';
import { FormattedMessage as F } from 'react-intl';
import * as ClipboardJS from 'clipboard';
import { FormattedMessage as F } from "react-intl";
import popup_mask from 'src/assets/tablecloth/table_mask.png';
import close from 'src/assets/close.png'
import { app_instance } from '../App';
import { Simulate } from 'react-dom/test-utils';
import copy = Simulate.copy;

interface ShareDialogProps {
  on_closed:() => void;
  player_name?:string;
  on_copy?:() => void;
}

interface ShareDialogState {
}


export class ShareDialog extends React.Component<ShareDialogProps, ShareDialogState > {
  
  protected clipboard?:ClipboardJS;
  protected copy_ref:React.RefObject<any>;
  private url:string;
  
  
  public constructor(props:ShareDialogProps) {
    super(props);
    this.state = {
    };
    this.copy_ref = React.createRef();
    this.url = window.location.origin + (props.player_name?('?ref=' + props.player_name):'')
  }
  
  public componentDidMount() {
    this.clipboard = new ClipboardJS(this.copy_ref.current, {
      text:() => {
        return this.url;
      }
    });
    this.clipboard.on('success', () => {
      app_instance.show_toast(<F id='copy' values={{
        value:this.url
      }}/>)
    })
  }
  
  public render(){
    return (
      <div style={{backgroundImage:`url(${popup_mask})`}} className='popup'>
        <div className='window'>
          <div className='header'>
            <text>
              <F id='go_to_have_money'/>
            </text>
            <img onClick={this.props.on_closed} src={close} className='close'/>
          </div>
          <div className='content_wrapper'>
            <div className='content'>
              <div style={{display:'flex', flexDirection:'row'}}>
                <div className='url'>
                  {this.url}
                </div>
                <div ref={this.copy_ref} className='copy_btn'>
                  <div className='box1'/>
                  <div className='box2'/>
                </div>
              </div>
              <div style={{textAlign:'left'}} className='rule_tips'><F id='money_tips'/></div>
            </div>
          </div>
        </div>
      </div>
    );
  }
  
}



